@font-face {
  font-family: "Bebas Neue";
  src: url("/fonts/BebasNeue-Regular.woff") format("woff"), url("/fonts/BebasNeue-Regular.woff2") format("woff2");
  font-weight: normal;
  font-style: normal; }

@font-face {
  font-family: "Bebas Neue";
  src: url("/fonts/BebasNeue-Bold.woff") format("woff"), url("/fonts/BebasNeue-Bold.woff2") format("woff2");
  font-weight: bold;
  font-style: normal; }

@font-face {
  font-family: "Roboto Condensed";
  src: url("/fonts/Roboto-Condensed.woff") format("woff"), url("/fonts/Roboto-Condensed.woff2") format("woff2");
  font-weight: normal;
  font-style: normal; }

@font-face {
  font-family: "Roboto Condensed";
  src: url("/fonts/RobotoCondensed-Light.woff") format("woff"), url("/fonts/RobotoCondensed-Light.woff2") format("woff2");
  font-weight: 300;
  font-style: normal; }

@font-face {
  font-family: "Roboto Condensed";
  src: url("/fonts/RobotoCondensed-Bold.woff") format("woff"), url("/fonts/RobotoCondensed-Bold.woff2") format("woff2");
  font-weight: bold;
  font-style: normal; }

.container {
  width: 1366px;
  margin: 0 auto; }

header {
  height: 658px;
  background: url("/img/header-bg.jpg");
  color: #FFFFFF; }
  header .logo,
  header .phone {
    font-family: Bebas Neue;
    font-size: 26px;
    line-height: 26px; }
  header .city-name {
    font-family: Bebas Neue;
    font-size: 49px;
    line-height: 49px; }
  header .fitness-name {
    font-family: Bebas Neue;
    font-size: 109px;
    line-height: 109px;
    text-align: right; }
  header .header-list {
    text-align: right;
    list-style: none; }
    header .header-list li {
      font-family: Roboto Condensed;
      font-size: 14px;
      line-height: 150%; }
  header .social-links {
    font-family: Roboto Condensed;
    font-size: 12px;
    line-height: 14px;
    text-align: right;
    text-transform: uppercase; }
